.result{
    margin-top: 48px;
    figure{
        width: 100%;
        background-color:#ebebeb;
        display: flex;
        flex-direction: column;
        align-items: center;
        h3{
            text-align: center;
            font-weight: 700;
            font-size: 24px;
            margin: 0;
        }
        img{
            width: 177px;
            height: 177px;
            background-color: cadetblue;
        }
        figcaption{
            width: 100%;
            font-size: 14px;
            display: flex;
            justify-content: center;
            margin: 6px 0 ;
            &.scores{
                font-weight: 700;
                font-size: 20px;
            }
            &.itemName{
                text-align: center;
                font-weight: 700;
                font-size: 20px;
                color:#1890ff;
            }
        }
        &>figcaption:nth-of-type(3){
            .icon-ul{
                li{
                    text-align: center;
                }
            }
            .msg-ul{
                padding-left: 12px;
            }
        }
    }
    .reset-btn{
        display: flex;
        justify-content: center
    }
}
@media screen and (max-width:992px) {
    .result{
        margin-top: 12px;
        figure{
            h3{
                font-size: 16px;
            }
            img{
                width: 80%;
                height: 125px;
            }
            figcaption{
                font-size: 12px;
                margin: 0 ;
                &.scores{
                    font-weight: 700;
                    font-size: 16px;
                }
                &.itemName{
                    font-weight: 700;
                    font-size: 16px;
                    color:#1890ff;
                }
            }
            &>figcaption:nth-of-type(3){
                .icon-ul{
                    li{
                        text-align: center;
                    }
                }
                .msg-ul{
                    padding-left: 12px;
                }
            }
        }
    }
}